const mutations: MutationTree<UserState> & Mutations = {
  [MutationTypes.CHANGE_USER_FIELD](state, payload) {
    state.user[payload.key] = payload.value
  },
  [MutationTypes.UPDATE_USER](state, payload) {
    state.user = payload
  }
}

